.default-cat-button {
    background-color: gray;
}

.selected-cat-button {
    background-color: pink;
}

body{
    font-family: sans-serif;
    background-color: #000000;
}

.page-content{
    margin-left:25px;
    margin-right:25px;
    background-color: #000000;
}

.header-box{
    width:100%;
    height: 290px;
    background-color:gray;
}

.header{
    display:flex;
    gap:20px;
    align-items:center;
    padding-top:20px;
    padding-left:20px;
}

.header-image {
    width:300px;
    height:250px;
    border-radius:20px;
}

.header-info{
    color:white;
}

        .header-title{
            font-size:70px;
            margin-top:30px;
            margin-bottom:30px;
        }

        .header-type{
            font-size:20px;
        }

        .header-desc{
            font-size:25px;
        }

        .song {
            width:99%;
        }

        .song-info{
            display:flex;
            align-items:center;
            text-align:left;
            color:white;
            padding-left:20px;
            padding-top:10px;
            padding-bottom:10px;
            }

            .song-number{
                margin-right:15px;
            }
            
            .song-title{
                font-weight:bold;
                width:35%;
                
                }
            
            .desc{
                width:57%
                }
            
            .play-button{
                background-color: #E54173;
                width:40px;
                height:40px;
                border-radius:50px;
            }

            .project-content{
                background-color:#222222;
                padding:20px;
                display:none;
            }



