

.profile-section{
    margin-bottom:50px;
}

    .section-title{
        font-size: 24px;
        color:white;
        margin-bottom: 20px;
        font-weight:bold;
    }

    .section-row{
        display:flex;
        /* flex-direction:row; */
        gap:15px;
    }

    .section-entry{
        width:200px;
        height:250px;
        background-color: #222222;
        border-radius:10px;
        padding:10px;
    }

        .entry-image{
            width:200px;
            height:150px;
            border-radius:10px;
        }
        
        .entry-title{
            color:white;
            font-weight:bold;
            font-size: 20px;
            width:100%;
            margin-top:10px;
            margin-bottom:5px;
        }

        .entry-desc{
            color:gray;
            font-size: 14px;
            width:100%;
        }






.popular-section{
    display:flex;
}

.popular-project{
    display:flex;
}

.about{
    color:azure; 
}
    .contact{
        font-weight:bold;
    }

    .contact-image{
        width:15px;
        height:15px;
    }

.toggles{
    display:flex;
    gap:10px;
    margin-bottom:20px;
}

.toggle {
    border-radius:20px;
    width:fit-content;
    background-color: #222222;
    color:white;
    font-weight:bold;
    padding-top:10px;
    padding-bottom:10px;
    padding-left:15px;
    padding-right:15px;

}

.popular-image{
    width:80px;
    height:80px;
    margin-right:20px;
}